<template>
	<div class="my">
		<div class="logo">
			<img src="../assets/images/logo.png" alt="">
			<span class="good">Good Luck</span>
		</div>
		<div class="personal">
			<div class="left">
				<h3 class="tips">{{nowTimer}}。</h3>
				<h2 v-if="!this.tokenString">Luck</h2>
				<h2 v-else>{{Personal.nickName}}</h2>
			</div>
			<!-- 头像 -->
			<div class="right">
				<div class="hands">
					<img v-if="!this.tokenString" class="auto-img" src="../assets/images/logo.png" alt="">
					<img v-else class="auto-img" :src="Personal.userImg" alt="">
				</div>
			</div>
		</div>
		<van-divider :style="{ color: '#21b276', borderColor: '#21b276', padding:'0px 10px' }">
			我的信息
		</van-divider>
		<div class="choice">
			<ul>
				<li @click="goPersonal">
					<div class="lefts">个人资料</div>
					<div class="rights"><van-icon name="arrow" /></div>
				</li>
				<li @click="goMyorder">
					<div class="lefts">我的订单</div>
					<div class="rights"><van-icon name="arrow" /></div>
				</li>
				<li>
					<div class="lefts">消费记录</div>
					<div class="rights"><van-icon name="arrow" /></div>
				</li>
				<li @click="Collection">
					<div class="lefts">我的收藏</div>
					<div class="rights"><van-icon name="arrow" /></div>
				</li>
				<li @click="goAddress">
					<div class="lefts">地址管理</div>
					<div class="rights"><van-icon name="arrow" /></div>
				</li>
				<li @click="goSecurity">
					<div class="lefts">安全中心</div>
					<div class="rights"><van-icon name="arrow" /></div>
				</li>
			</ul>
		</div>
		<div class="login">
			<van-button type="primary" block round @click="goLogin">登 录/切换账号</van-button>
		</div>
	</div>
</template>

<script>
	import "../assets/less/My.less"
	export default {
		name: "My",
		data() {
			return {
				//个人资料
				Personal:[],
				tokenString:""
			}
		},
		computed: {
			nowTimer: () => {
				let date = new Date()
				let hours = date.getHours()
				let greetings = ""
				if (hours >= 6 && hours <= 12) {
					greetings = "早上好"
				}
				if (hours >= 12 && hours <= 18) {
					greetings = "下午好"
				}
				if (hours >= 18 && hours <= 24) {
					greetings = "晚上好"
				}
				if (hours >= 1 && hours <= 6) {
					greetings = "深夜好"
				}
				return greetings
			}
		},
		created() {
			this.getPersonal()
		},
		methods: {
			//进入我的订单页面
			goMyorder(){
				this.$router.push({name:"OrderPage"})
			},
			//进入登录页面
			goLogin(){
				this.$router.push({name:"Login"})
			},
			//进入个人资料
			goPersonal(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({name: "Login"})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.$router.push({name:"PersonalData"})
			},
			//进入个人收藏
			Collection(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({name: "Login"})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					duration: 500
				});
				this.$router.push({name:"Collection"})
			},
			//进入地址管理
			goAddress(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({name: "Login"})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.$router.push({name:"Address"})
			},
			//进入安全中心
			goSecurity(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return this.$router.push({name: "Login"})
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.$router.push({name:"Security"})
			},
			//获取我的资料
			getPersonal(){
				this.tokenString = this.$cookies.get("tokenString");
				if (!this.tokenString) {
					//如果不存在token值,直接return
					return;
				}
				this.$toast.loading({
					message: '加载中...',
					forbidClick: true,
					dutation: 500
				});
				this.axios({
					method: 'GET',
					url: '/findMy',
					params: {
						tokenString: this.tokenString
					}
					//如果是post请求,参数需要挂载到data
				}).then(res => {
					//如果请求成功清掉提示
					// console.log(res);
					if (res.data.code == 700) {
						return
					}
					if (res.data.code === 'A001') {
						this.Personal=res.data.result[0]
						// console.log(this.Personal);
					}
				}).catch(eer => {
					console.log("eer==>", eer);
				})
			},
		}
	}
</script>
